.card-container {
    display: flex;
    flex-direction: column;
    background-color: #95dada;
    border:1px solid grey;
    border-radius: 5px;
    padding: 25px;
    cursor: pointer;
    backface-visibility: hidden;
    transform: translate(0);
    transition: transform 0.25s ease-out;
}

.card-container:hover {
    transform: scale(1.05);
}

.card-container img{

    width:180px; /* Only need to set width height will be auto*/
    
    margin:0 auto; /* Set no margin on top and bottom and center (auto) left and right*/
    
}